*{
  font-family: Arial, Helvetica, sans-serif;

}
body{
  padding: 60px;
}
input{
  width: 100%;
  font-size: 1.2rem;
  color: grey;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
  height: 160px;
  width: 300px;
  
}
.card p{
  padding: 5px;
  text-align: center;

}
.card img{
  max-height: 100px;
  width: 95%;
  margin: 7px 7px 0 7px;
}